import { instance } from './api';
export var authAPI = {
    me: function () {
        return instance.get("auth/me").then(function (res) { return res.data; });
    },
    login: function (email, password, rememberMe, captcha) {
        return instance.post("auth/login", { email: email, password: password, rememberMe: rememberMe, captcha: captcha }).then(function (res) { return res.data; });
    },
    logout: function () {
        return instance.delete("auth/login").then(function (res) { return res.data; });
    },
    getCaptcha: function () {
        return instance.get("security/get-captcha-url").then(function (res) { return res.data; });
    }
};
